@layer base {
  :root {
    --background: 0 0% 3%;
    --foreground: 0 0% 95%;
    --card: 0 0% 8%;
    --card-foreground: 0 0% 90%;
    --popover: 0 0% 5%;
    --popover-foreground: 0 0% 95%;
    --primary: 200 100% 60%;
    --primary-foreground: 0 0% 5%;
    --secondary: 220 20% 25%;
    --secondary-foreground: 0 0% 90%;
    --muted: 220 15% 15%;
    --muted-foreground: 220 10% 70%;
    --accent: 210 80% 70%;
    --accent-foreground: 0 0% 5%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 210 40% 98%;
    --border: 220 20% 20%;
    --input: 220 20% 15%;
    --ring: 200 100% 60%;
    --cyber-blue: 200 100% 60%;
    --cyber-blue-glow: 200 100% 70%;
    --steel: 220 20% 30%;
    --steel-light: 220 25% 40%;
    --steel-dark: 220 15% 20%;
    --neon-white: 0 0% 95%;
    --glass: 220 20% 15%;
    --gradient-hero: linear-gradient(135deg, hsl(var(--background)), hsl(var(--steel-dark)));
    --gradient-card: linear-gradient(145deg, hsl(var(--card)), hsl(var(--steel-dark)));
    --gradient-metallic: linear-gradient(135deg, hsl(var(--steel)), hsl(var(--steel-light)));
    --gradient-neon: linear-gradient(90deg, hsl(var(--cyber-blue)), hsl(var(--accent)));
    --shadow-neon: 0 0 20px hsl(var(--cyber-blue) / 0.3);
    --shadow-metallic: 0 8px 32px hsl(var(--steel-dark) / 0.4);
    --shadow-glass: 0 8px 32px hsl(0 0% 0% / 0.3);
    --radius: 0.75rem;
  }
  body {
    overflow-x: hidden;
    background-color: var(--background);
    color: var(--foreground);
    background: var(--gradient-hero);
    font-family: 'Inter', 'Segoe UI', sans-serif;
  }
  ::-webkit-scrollbar {
    width: 8px;
  }
  ::-webkit-scrollbar-track {
    background: hsl(var(--background));
  }
  ::-webkit-scrollbar-thumb {
    background: var(--gradient-neon);
    border-radius: 4px;
  }
  ::-webkit-scrollbar-thumb:hover {
    background: hsl(var(--cyber-blue-glow));
  }
}
@layer components {
  .neon-glow {
    box-shadow: var(--shadow-neon);
    transition: all 0.3s ease;
  }
  .neon-glow:hover {
    box-shadow: 0 0 30px hsl(var(--cyber-blue) / 0.5);
    transform: translateY(-2px);
  }
  .glass-card {
    background: var(--gradient-card);
    backdrop-filter: blur(10px);
    border: 1px solid hsl(var(--border));
    box-shadow: var(--shadow-glass);
  }
  .btn-metallic {
    background: var(--gradient-metallic);
    border: 1px solid hsl(var(--steel-light));
    box-shadow: var(--shadow-metallic);
    transition: all 0.3s ease;
  }
  .btn-metallic:hover {
    transform: translateY(-3px);
    box-shadow: var(--shadow-neon), var(--shadow-metallic);
  }
  .cyber-text {
    color: hsl(var(--cyber-blue));
    text-shadow: 0 0 10px hsl(var(--cyber-blue) / 0.5);
  }
}
@layer utilities {
  .animate-slide-in-up {
    animation: slide-in-up 0.6s ease both;
  }
  .animate-slide-in-left {
    animation: slide-in-left 0.6s ease both;
  }
  .animate-pulse-neon {
    animation: pulse-neon 2s ease-in-out infinite;
  }
  .animate-float {
    animation: floaty 6s ease-in-out infinite;
  }
}
@keyframes slide-in-up {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slide-in-left {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes pulse-neon {
